angular.module("templates", []).run(["$templateCache", function($templateCache) {$templateCache.put("components/date-time/date-time.html","<div class=\"date-time-wrapper\">\n  <span class=\"date-time-wrapper__time\">{{vm.currentTime}}</span><br />\n  <span class=\"date-time-wrapper__date\">{{vm.todaysDate}}</span>\n</div>");
$templateCache.put("components/weather-forecast/weather-forecast.html","<h3 class=\"user-location\">{{vm.address}}</h3>\n\n<div>\n\n  <ul class=\"forecast-list\">\n\n      <li class=\"forecast-list__list-item col-md-1\"\n          ng-repeat=\"day in vm.hourlyForecast.data | limitTo: 8 track by $index\">\n        <div>\n          {{day.time | convertHourlyTime}}<br />\n          <i class=\"{{day.icon | convertIconEmoji}}\"></i><br />\n          {{vm.Math.round(day.temperature)}}<br />\n        </div>\n      </li>\n\n  </ul>\n\n  <ul class=\"forecast-list\">\n\n      <li class=\"forecast-list__list-item col-md-1\"\n          ng-repeat=\"day in vm.dailyForecast.data | limitTo: 5 track by $index\"\n          ng-mouseover=\"showSummary = true\" ng-mouseleave=\"showSummary = false\">\n        {{day.time | convertDailyTime}}<br />\n        <i class=\"{{day.icon | convertIconEmoji}}\"></i><br />\n        {{vm.Math.floor(day.temperatureMax)}} / {{vm.Math.floor(day.temperatureMin)}}\n\n        <div ng-show=\"showSummary\">\n          {{day.summary}}<br />\n        </div>\n\n      </li>\n\n  </ul>\n\n</div>");}]);